import Link from "next/link";
import { User, DocumentText, Briefcase, LogoutCurve } from "iconsax-react";

export default function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    return (
        <div className="flex h-screen bg-gray-50 text-dark">
            {/* Sidebar */}
            <aside className="w-64 bg-white border-r border-gray-200 flex flex-col">
                <div className="h-16 flex items-center px-6 border-b border-gray-100">
                    <span className="font-black text-xl tracking-tighter">ADMIN</span>
                </div>

                <nav className="flex-1 p-4 space-y-2">
                    <Link href="/admin" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-600 hover:bg-gray-50 hover:text-primary transition-colors">
                        <User size="20" variant="Bold" />
                        <span className="font-bold text-sm">Dashboard</span>
                    </Link>
                    <Link href="/admin/blogs" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-600 hover:bg-gray-50 hover:text-primary transition-colors">
                        <DocumentText size="20" variant="Bold" />
                        <span className="font-bold text-sm">Blogs</span>
                    </Link>
                    <Link href="/admin/projects" className="flex items-center gap-3 px-4 py-3 rounded-lg text-gray-600 hover:bg-gray-50 hover:text-primary transition-colors">
                        <Briefcase size="20" variant="Bold" />
                        <span className="font-bold text-sm">Portfolio</span>
                    </Link>
                </nav>

                <div className="p-4 border-t border-gray-100">
                    <button className="flex items-center gap-3 px-4 py-3 rounded-lg text-red-500 hover:bg-red-50 w-full transition-colors">
                        <LogoutCurve size="20" variant="Bold" />
                        <span className="font-bold text-sm">Logout</span>
                    </button>
                </div>
            </aside>

            {/* Main Content */}
            <main className="flex-1 overflow-auto">
                <header className="h-16 bg-white border-b border-gray-200 px-8 flex items-center justify-between">
                    <h2 className="font-bold text-lg">Dashboard</h2>
                    <div className="w-8 h-8 rounded-full bg-primary text-white flex items-center justify-center font-bold">A</div>
                </header>
                <div className="p-8">
                    {children}
                </div>
            </main>
        </div>
    );
}
